import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { DeliveryGhnComponent } from './delivery-ghn.component';
import { IonicModule } from '@ionic/angular';
import { NgZorroAntModule } from 'src/app/ng-zorro-ant.module';
import { TranslateModule } from '@ngx-translate/core';
import { RouterModule, Routes } from '@angular/router';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { CreateBillRetailComponent } from './create-bill-retail/create-bill-retail.component';
import { CreateBillCustomerComponent } from './create-bill-customer/create-bill-customer.component';
import { ReuseBillRetailComponent } from './reuse-bill-retail/reuse-bill-retail.component';
import { ReuseBillCustomerComponent } from './reuse-bill-customer/reuse-bill-customer.component';
import { DetailBillComponent } from './detail-bill/detail-bill.component';
import { ChooseProvinceGhnComponent } from './components/choose-province-ghn/choose-province-ghn.component';
import { ChooseDistrictGhnComponent } from './components/choose-district-ghn/choose-district-ghn.component';
import { ChooseWardGhnComponent } from './components/choose-ward-ghn/choose-ward-ghn.component';
import { ChooseMethodGhnComponent } from './components/choose-method-ghn/choose-method-ghn.component';
import { AddAddressCustomerGhnComponent } from './components/add-address-customer-ghn/add-address-customer-ghn.component';
import { EditAddressCustomerGhnComponent } from './components/edit-address-customer-ghn/edit-address-customer-ghn.component';
import { ListAddressCustomerGhnComponent } from './components/list-address-customer-ghn/list-address-customer-ghn.component';
import { ShippingBillHistoryComponent } from './components/shipping-bill-history/shipping-bill-history.component';

const routes: Routes = [
  {
    path: "",
    children: [
      {
        path: "",
        component: DeliveryGhnComponent
      },
      {
        path: "order-bill-retail",
        component: CreateBillRetailComponent
      },
      {
        path: "detail-bill",
        component: DetailBillComponent
      },
      {
        path: 'order-bill-customer',
        component: CreateBillCustomerComponent
      },
      {
        path: 'reuse-info-retail',
        component: ReuseBillRetailComponent
      },
      {
        path: 'reuse-info-customer',
        component: ReuseBillCustomerComponent
      }
    ]
  },
]


@NgModule({
  declarations: [DeliveryGhnComponent, CreateBillRetailComponent, CreateBillCustomerComponent, ReuseBillRetailComponent, ReuseBillCustomerComponent, DetailBillComponent, ChooseProvinceGhnComponent, ChooseDistrictGhnComponent, ChooseWardGhnComponent, ChooseMethodGhnComponent, AddAddressCustomerGhnComponent, EditAddressCustomerGhnComponent, ListAddressCustomerGhnComponent, ShippingBillHistoryComponent],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    NgZorroAntModule,
    IonicModule,
    FormsModule,
    TranslateModule,
    ReactiveFormsModule
  ]
})
export class DeliveryGhnModule { }
