import { Component, Input, OnInit } from '@angular/core';
import { EventEmitter, Output } from '@angular/core';
import { VhAlgorithm } from 'ionic-vhframeworks';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { GhnService } from 'src/app/sales/menu/manage/delivery/services/ghnService/ghn.service';
import { LanguageService } from 'src/app/sales/services/language.service';

@Component({
  selector: 'app-choose-province-ghn',
  templateUrl: './choose-province-ghn.component.html',
  styleUrls: ['./choose-province-ghn.component.scss']
})
export class ChooseProvinceGhnComponent implements OnInit {
  @Input("chooseProvinceGhn") chooseProvinceGhn;
  @Input("token") token;
  @Output("cancelModalProvinceGhn") cancelModalProvinceGhn = new EventEmitter<any>();
  search: string = '';
  constructor(
    private ghnService: GhnService,
    private vhComponent: VhComponent,
    private languageService: LanguageService,
    private vhAlgorithm: VhAlgorithm
  ) { }
  ngOnInit() {
    this.getProvince();
  };
  list: any = [];
  sublist: any = [];
  setHeight = {
    "height": "400px"
  }
  dismiss() {
    this.cancelModalProvinceGhn.emit(false);
  }
  getProvince() {
    this.vhComponent.showLoading("", "transparent-loading").then(() => {
      this.ghnService.getProvinceGhn(this.token).then((res: any) => {
        this.vhComponent.hideLoading(0);
        this.list = res.data;
        this.sublist = this.list;
      }).catch(err => {
        this.vhComponent.hideLoading(0);
        this.vhComponent.showToast(2000, this.languageService.translate('An error occured while getting provinces !'), "alert-toast");
      })
    })
  }
  chooseProvince(item) {
    this.cancelModalProvinceGhn.emit(item);
  }
  searchBar(value) {
    if (value.trim().length) {
      this.list = this.vhAlgorithm.searchList(this.vhAlgorithm.changeAlias(value.toLowerCase()), this.sublist, ["ProvinceName"])
    }
    else {
      this.list = this.sublist;
    }
  }
}
