import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { VhAlgorithm, VhAuth, VhQuery, VhQuerySales } from 'ionic-vhframeworks';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { GhnService } from 'src/app/sales/menu/manage/delivery/services/ghnService/ghn.service';
import { LanguageService } from 'src/app/sales/services/language.service';

@Component({
  selector: 'app-delivery-ghn',
  templateUrl: './delivery-ghn.component.html',
  styleUrls: ['./delivery-ghn.component.scss']
})
export class DeliveryGhnComponent implements OnInit {
  bills: Array<any> = [];
  show_bills: any = [];
  //Hm nay 0h- 23h59
  startDateTime = new Date();
  subStartDateTime = new Date();
  endDateTime = new Date();
  subEndDateTime = new Date();
  //bin c show
  status_open: boolean = false;
  data_delivery: any;
  valueOfStatus: number = 1;
  search: string = '';
  list_status: Array<any> = [
    {
      value: 1,
      name: "All"
    },
    {
      value: 0,
      name: "Not enough information"
    },
    {
      value: 2,
      name: "Ready to pick"
    },
    {
      value: 3,
      name: "Picked"
    },
    {
      value: 4,
      name: "Delivered"
    },
    {
      value: 5,
      name: "Returning"
    },
    {
      value: 6,
      name: "Returning"
    },
  ]
  disabledDate = (current: Date): boolean => {
    if (current.getTime() < new Date(this.vhAuth.getStore().registrationdate).getTime() - 24 * 60 * 60 * 1000) return true
    return false
  }
  constructor(
    public languageService: LanguageService,
    private router: Router,
    private vhQuery: VhQuery,
    private vhComponent: VhComponent,
    public vhQuerySales: VhQuerySales,
    public vhAlgorithm: VhAlgorithm,
    private ghnService: GhnService,
    private cdRef: ChangeDetectorRef,
    private vhAuth: VhAuth
  ) {
    this.startDateTime.setHours(0, 0, 0);
    this.subStartDateTime = this.startDateTime;
    this.endDateTime.setHours(23, 59, 59);
    this.subEndDateTime = this.endDateTime;
    this.getSearchAndDate();
  }

  ngOnInit(): void {
    this.getData(this.startDateTime, this.endDateTime);
  }
  goBack() {
    this.router.navigate(["/sales/dashboard/manage/delivery/shipping-bill"]);
  }
  getSearchAndDate() {
    if (this.router.getCurrentNavigation().extras.state) {
      let dataSearchAndDate = this.router.getCurrentNavigation().extras.state.dataSearchAndDate;
      this.search = dataSearchAndDate.search ? dataSearchAndDate.search : "";
      this.startDateTime = dataSearchAndDate.startDateTime;
      this.endDateTime = dataSearchAndDate.endDateTime;
      this.subStartDateTime = this.startDateTime;
      this.subEndDateTime = this.endDateTime;
    }
  }
  /**
   * Hm ly bill trong khong thi gian c set
   */
  getData(startTime, endTime) {
    this.vhComponent.showLoading("").then(() => {
      Promise.all([
        this.vhQuerySales.getBills_byFields( { date: { $gte: startTime, $lte: endTime }, bill_type: { $eq: 1 }, id_branch: { $eq: this.vhQuerySales.getDefaultBranch()._id } }, {}), //cha bill_type
        this.vhQuerySales.getlocalDeliverys()
      ]).then((res: any) => {
        if (res[1]) this.data_delivery = res[1].filter(item => item.active == true && item.type == 1)[0];
        if (res[0]) {
          this.bills = res[0].filter(item => item.id_delivery == this.data_delivery._id).reverse();
          let process: Array<any> = []
          this.bills.forEach(item => {
            item.name_customer = this.vhQuerySales.getlocalCustomer(item.id_customer).name
            if (item.order_code)
              process.push(this.ghnService.getInfoOrder(this.data_delivery.token, item.order_code))
          })
          Promise.all(process).then((result: Array<any>) => {
            result.forEach(item => {
              let index = this.bills.findIndex(data => data.order_code == item.data.order_code)
              if (index >= 0) {
                this.bills[index].data = item.data
                this.bills[index].group = this.filterGroup(item.data.status);
              }
            })
            this.vhComponent.hideLoading(0);
            this.show_bills = [...this.bills];
            this.status_open = true;
          }).catch(err => {
            this.vhComponent.hideLoading(0);
            this.vhComponent.showToast(2000, this.languageService.translate("An error occurred while getting data"), "alert-toast")
          })
        }
      })
    })
  }
  filterGroup(status) {
    if (status == 'ready_to_pick' || status == 'picking' || status == 'money_collect_picking') return 2; // ch ly hng
    else if (status == 'picked' || status == 'storing' || status == 'transporting' || status == 'sorting' || status == 'delivering' ||
      status == 'money_collect_delivering' || status == 'delivery_fail') return 3; //ang giao hng
    else if (status == 'delivered') return 4//giao hng thnh cng
    else if (status == 'waiting_to_return' || status == 'return' || status == 'return_transporting' || status == 'return_sorting'
      || status == 'returning' || status == 'return_fail') return 5//ang hon hnh
    else if (status == 'returned') return 6; // hon hng thnh cng
    else if (status == 'cancel') return 7;
    else return 0
  }
  setStartTime(event) {
    if (this.endDateTime.getTime() - this.startDateTime.getTime() > 2678400000) {
      this.vhComponent.showToast(2000, this.languageService.translate("The time to view no more than 30 days"), "alert-toast")
      this.endDateTime = new Date(this.startDateTime.getTime() + 2678313600)
      this.subStartDateTime = this.startDateTime
      this.subEndDateTime = this.endDateTime
      this.getData(this.startDateTime, this.endDateTime);
    }
    else if (this.endDateTime.getTime() - this.startDateTime.getTime() < 0) this.vhComponent.alertMessage("", this.languageService.translate("Staring date must be less than or the same as ending date"), "", "OK");
    else this.getData(this.startDateTime, this.endDateTime);
  }
  setEndTime(event) {
    if (this.endDateTime.getTime() - this.startDateTime.getTime() > 2678400000) {
      this.vhComponent.showToast(2000, this.languageService.translate("The time to view no more than 30 days"), "alert-toast")
      this.startDateTime = new Date(this.endDateTime.getTime() - 2678313600)
      this.subStartDateTime = this.startDateTime
      this.subEndDateTime = this.endDateTime
      this.getData(this.startDateTime, this.endDateTime);
    }
    else if (this.endDateTime.getTime() - this.startDateTime.getTime() < 0) this.vhComponent.alertMessage("", this.languageService.translate("Staring date must be less than or the same as ending date"), "", "OK");
    else this.getData(this.startDateTime, this.endDateTime);
  }
  //searchbill
  searchBill(value) {
    this.show_bills = this.vhAlgorithm.searchList(
      this.vhAlgorithm.changeAlias(value).toLowerCase(),
      this.bills,
      ["bill_code", "order_code"]
    );
  }
  gotoDetail(bill) {
    if (bill.id_customer != "id_retail") {
      this.router.navigate(["/sales/dashboard/manage/delivery/shipping-bill/delivery-ghn/order-bill-customer"], {
        state: {
          billInfo: bill,
          data_delivery: this.data_delivery,
          dataSearchAndDate: {
            search: this.search,
            startDateTime: this.startDateTime,
            endDateTime: this.endDateTime
          }
        }
      });
    }
    else {
      this.router.navigate(["/sales/dashboard/manage/delivery/shipping-bill/delivery-ghn/order-bill-retail"], {
        state: {
          billInfo: bill,
          data_delivery: this.data_delivery,
          dataSearchAndDate: {
            search: this.search,
            startDateTime: this.startDateTime,
            endDateTime: this.endDateTime
          }
        }
      });
    }
  }
  gotoBillDetail(bill) {
    this.router.navigate(["/sales/dashboard/manage/delivery/shipping-bill/delivery-ghn/detail-bill"],
      {
        state: {
          billInfo: bill,
          data_delivery: this.data_delivery,
          dataSearchAndDate: {
            search: this.search,
            startDateTime: this.startDateTime,
            endDateTime: this.endDateTime
          }
        }
      })
  }
  /**
  * filter cc trng thi ca bill 
  * */
  filterStatus(value) {
    value = parseInt(value);
    if (value == 0) {
      this.show_bills = this.bills.filter(item => !item.data);
    }
    else if (value == 1) {
      this.show_bills = this.bills;
    }
    else if (value == 2) {
      this.show_bills = this.bills.filter(item => item.group == 2)
    }
    else if (value == 3) {
      this.show_bills = this.bills.filter(item => item.group == 3)
    }
    else if (value == 4) {
      this.show_bills = this.bills.filter(item => item.group == 4)
    }
    else if (value == 5) {
      this.show_bills = this.bills.filter(item => item.group == 5)
    }
    else if (value == 6) {
      this.show_bills = this.bills.filter(item => item.group == 6)
    }
  }
  onCurrentPageDataChange(event) { }
  heightScroll: string;
  ngAfterViewChecked() {
    if (document.getElementById("table-delivery-ghn") && document.querySelector(".delivery-ghn-header") && document.querySelector(".ant-table-pagination")) {
      this.heightScroll = document.getElementById("table-delivery-ghn").clientHeight - document.querySelector(".delivery-ghn-header").clientHeight -
        document.querySelector(".ant-table-pagination").clientHeight - 150 + "px";
    }
    this.cdRef.detectChanges();

  }
}
