import { Component, Input, OnInit, Output, EventEmitter } from '@angular/core';
import { VhAlgorithm } from 'ionic-vhframeworks';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { GhnService } from 'src/app/sales/menu/manage/delivery/services/ghnService/ghn.service';
import { LanguageService } from 'src/app/sales/services/language.service';

@Component({
  selector: 'app-choose-method-ghn',
  templateUrl: './choose-method-ghn.component.html',
  styleUrls: ['./choose-method-ghn.component.scss']
})
export class ChooseMethodGhnComponent implements OnInit {
  @Input("dataToGetMethod") dataToGetMethod;
  @Input("chooseMethod") chooseMethod;
  @Output("cancelModalMethodGhn") cancelModalMethodGhn = new EventEmitter<any>();
  roadService: any;
  flightService: any;
  data: any = [];
  setHeight = {
    "height": "400px"
  }
  constructor(
    private ghnService: GhnService,
    private languageService: LanguageService,
    private vhComponent: VhComponent,
    public vhAlgorythm: VhAlgorithm
  ) { }

  ngOnInit() {
    this.getData();
  }
  getData() {
    for (let i in this.dataToGetMethod.service) {
      if (this.dataToGetMethod.service[i].service_type_id == 1) {
        this.getServiceFlight(this.dataToGetMethod.service[i].service_id);
      }
      else if (this.dataToGetMethod.service[i].service_type_id == 2) {
        this.getServiceRoad(this.dataToGetMethod.service[i].service_id);
      }
    }
  }
  /**
  *Hm tr v thng tin  chun b cho vo cc hm getService
  **/
  prepareInfo(service_id) {
    let info = {
      shop_id: this.dataToGetMethod.shop_id,
      to_district_id: this.dataToGetMethod.to_district_id,
      to_ward_code: this.dataToGetMethod.to_ward_code,
      height: this.dataToGetMethod.height,
      length: this.dataToGetMethod.length,
      weight: this.dataToGetMethod.weight,
      width: this.dataToGetMethod.width,
      insurance_value: this.dataToGetMethod.insurance_value,
      service_id: service_id,
      from_district_id: this.dataToGetMethod.from_district_id,
      from_ward_code: this.dataToGetMethod.from_ward_code
    }
    return info;
  }
  getServiceRoad(service_id) {
    this.vhComponent.showLoading('');
    let info = this.prepareInfo(service_id);
    this.ghnService.calculate_delivery_fee(this.dataToGetMethod.token, info).then((res: any) => {
      if (res.data) {
        this.ghnService.calculateTheExpectedDeliveryTime(this.dataToGetMethod.token, info).then((i: any) => {
          this.vhComponent.hideLoading(0);
          this.roadService = {
            service_id: info.service_id,
            sort_name: this.languageService.translate('Road'),
            leadtime: new Date(i.data.leadtime * 1000),
            ...res.data
          }
        }).catch(err => {
          this.vhComponent.hideLoading(0);
          this.vhComponent.showToast(2000, this.languageService.translate("Invalid data, please check again"), "alert-toast")
        })
      }
    }).catch(
      err => {
        this.vhComponent.hideLoading(0);
        this.vhComponent.showToast(2000, this.languageService.translate("Invalid data, please check again"), "alert-toast")
      }
    )
  }
  getServiceFlight(service_id) {
    let info = this.prepareInfo(service_id);
    this.ghnService.calculate_delivery_fee(this.dataToGetMethod.token, info).then((res: any) => {
      if (res.data) {
        this.ghnService.calculateTheExpectedDeliveryTime(this.dataToGetMethod.token, info).then((i: any) => {
          this.vhComponent.hideLoading(0);
          this.flightService = {
            service_id: info.service_id,
            sort_name: this.languageService.translate('Flight'),
            leadtime: new Date(i.data.leadtime * 1000),
            ...res.data
          }
        }).catch(err => {
          this.vhComponent.hideLoading(0);
          this.vhComponent.showToast(2000, this.languageService.translate("Invalid data, please check again"), "alert-toast")
        })
      }
    }).catch(
      err => {
        this.vhComponent.hideLoading(0);
        this.vhComponent.showToast(2000, this.languageService.translate("Invalid data, please check again"), "alert-toast")
      }
    )
  }
  dismiss() {
    this.cancelModalMethodGhn.emit(false);
  }
  chooseService(item) {
    this.cancelModalMethodGhn.emit(item)
  }


}
