import { Component, Input, OnInit } from '@angular/core';
import { EventEmitter, Output } from '@angular/core';
import { VhAlgorithm } from 'ionic-vhframeworks';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { GhnService } from 'src/app/sales/menu/manage/delivery/services/ghnService/ghn.service';
import { LanguageService } from 'src/app/sales/services/language.service';
@Component({
  selector: 'app-choose-ward-ghn',
  templateUrl: './choose-ward-ghn.component.html',
  styleUrls: ['./choose-ward-ghn.component.scss']
})
export class ChooseWardGhnComponent implements OnInit {

  @Input("chooseWardGhn") chooseWardGhn;
  @Input("district_idModal") district_idModal;
  @Input("token") token;
  @Output("cancelModalWardGhn") cancelModalWardGhn = new EventEmitter<any>();
  search: string = '';
  constructor(
    private ghnService: GhnService,
    private vhComponent: VhComponent,
    private languageService: LanguageService,
    private vhAlgorithm: VhAlgorithm
  ) { }
  ngOnInit() {
    this.getProvince();
  };
  list: any = [];
  sublist: any = [];
  setHeight = {
    "height": "400px"
  }
  dismiss() {
    this.cancelModalWardGhn.emit(false);
  }
  getProvince() {
    this.vhComponent.showLoading("", "transparent-loading").then(() => {
      this.ghnService.getWardGhn(this.token, this.district_idModal).then((res: any) => {
        this.vhComponent.hideLoading(0);
        this.list = res.data;
        this.sublist = this.list;
      }).catch(err => {
        this.vhComponent.hideLoading(0);
        this.vhComponent.showToast(2000, this.languageService.translate('An error occured while getting district !'), "alert-toast");
      })
    })
  }
  chooseProvince(item) {
    this.cancelModalWardGhn.emit(item);
  }
  searchBar(value) {
    if (value.trim().length) {
      this.list = this.vhAlgorithm.searchList(this.vhAlgorithm.changeAlias(value.toLowerCase()), this.sublist, ["WardName"])
    }
    else {
      this.list = this.sublist;
    }
  }
}
