import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';

@Component({
  selector: 'app-shipping-bill-history',
  templateUrl: './shipping-bill-history.component.html',
  styleUrls: ['./shipping-bill-history.component.scss']
})
export class ShippingBillHistoryComponent implements OnInit {
  @Input("visible_history") visible_history: boolean = false;
  @Input("data_history_bills") data_history_bills;
  @Output("onCloseHistoryBill") onCloseHistoryBill = new EventEmitter<any>();
  constructor() { }

  ngOnInit(): void {
  }
  onClose() {
    this.onCloseHistoryBill.emit(false);
  }
}
